import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, Link,withRouter } from "react-router-dom";
import Button from '@material-ui/core/Button';
import clsx from 'clsx';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import PropType from 'prop-types';
import ClientesService from '../../servicios/cliente-service';
import Grilla from '../grilla/grilla'

const useStyles = makeStyles(theme => ({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    textField: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
      backgroundColor: theme.palette.primary
    },
    dense: {
      marginTop: theme.spacing(2),
    },
    menu: {
      width: 200,
    },
  }));


  function Example() {
    // Declara una nueva variable de estado, que llamaremos "count".
    const [count, setCount] = useState(0);

    // Similar a componentDidMount y componentDidUpdate:
    useEffect(() => {
        // Actualiza el t√≠tulo del documento usando la Browser API
        document.title = `You clicked ${count} times`;
    });

    return (
        <div>
            <p>You clicked {count} times</p>
            <Button type="button" 
                    variant="contained" 
                    color="primary"
                    onClick={() => setCount(count + 1)}>
                        Click me
                    </Button>

        </div>
    );
}


class Formulario extends React.Component{
    cs;
    constructor(props){
        super(props);
        this.state ={
          nombre:"",
          apellido:"",
          cta_cte:0,
          compras:[]
        }
        this.cs = new ClientesService()
    }

    componentDidMount() {

      if (this.props.location.state) {
        this.setState({...this.props.location.state.cliente})
      }
    }
    
    handleChange =(e) => {
        //console.log("evento: ",e);
        let name = e.target.name;
        this.setState({
        [name]: e.target.value
        })
        
    }
    
     enviarForm = async (e)  => {
      e.preventDefault();
      //this.props.custom(e)
      
      
      let cliModel = new cliente(this.state.nombre,
                                  this.state.apellido,
                                  Number(this.state.id),
                                  Number(this.state.cta_cte),
                                  this.state.compras)
      let cliFinal = new ClienteDTO(cliModel);
      
      const a = await this.cs.addClienteMongo(cliFinal);
      if (a == true) {
        this.props.history.push("/clientes")
      }
      
      

    }



    render(){

        return(
            <div>
                <form className={useStyles.container} onSubmit={this.enviarForm}>
                    <TextField
                        id="outlined-name"
                        label="nombre"
                        name="nombre"
                        className={useStyles.textField}
                        value={this.state.nombre}
                        // onChange={handleChange('name')}
                        onChange={this.handleChange}
                        margin="normal"
                        variant="outlined"
                    />
                    <br/>
                    <TextField
                        id="standar-name"
                        label="Apellido"
                        name="apellido"
                        className={useStyles.textField}
                        value={this.state.apellido}
                        // onChange={handleChange('name')}
                        onChange={this.handleChange}
                        margin="normal"
                        variant="outlined"
                    />
                    {/* <br/>
                    <TextField
                        id="standar-name"
                        label="Cuenta corriente"
                        name="cta_cte"
                        type="number"
                        className={useStyles.textField}
                        value={this.state.cta_cte}
                        // onChange={handleChange('name')}
                        onChange={this.handleChange}
                        margin="normal"
                        variant="outlined"
                    /> */}
                    <br/>
                    <Button 
                      type="submit" 
                      variant="contained" 
                      color="primary">
                        Enviar
                    </Button>
                </form>
                
            </div>
        )
    }

}

export default withRouter(Formulario);


class cliente{

  constructor(nombre,apellido,id,cta_cte, compras){
    this.nombre = nombre;
    this.apellido = apellido;
    this.id = id;
    this.cta_cte = cta_cte;
    this.compras = compras;
  }

}

class ClienteDTO{
  constructor(cliente){
    this.cliente = cliente;
  }
}

class Venta{

  constructor(producto,fecha,monto,cobrado){
    this.producto = producto;
    this.fecha = fecha;
    this.monto = monto;
    this.cobrado = cobrado;
  }

}